import { Watchlist } from "@rp/common/api-types"
import {
  mockBriefConfigs,
  useBriefConfigById,
  useUserBriefConfigs,
} from "@rp/react/tanstack-api/brief"
import { useAllWatchlists, useWatchlist } from "@rp/react/tanstack-api/watchlist"
import { useBriefCreateOrEditSteps, useCreateBriefCallbacks } from "@rp/react/web/brief"
import { renderWithProviders } from "@rp/react/web/common/tools"
import { fireEvent, screen, within } from "@testing-library/dom"
import { BriefCreateOrEdit } from "./BriefCreateOrEdit"

const mockUseBriefCreateOrEditSteps = useBriefCreateOrEditSteps as jest.Mock
const mockUseCreateBriefCallbacks = useCreateBriefCallbacks as jest.Mock

jest.mock("@rp/react/web/brief", () => ({
  ...jest.requireActual("@rp/react/web/brief"),
  usePreloadForCreateBriefFromWatchlistScreen: jest.fn(),
  usePreloadForEditBrief: jest.fn(),
  useBriefCreateOrEditSteps: jest.fn(),
  useCreateBriefCallbacks: jest.fn(),
}))

const mockUseWatchlist = useWatchlist as jest.Mock
const mockUseAllWatchlists = useAllWatchlists as jest.Mock

jest.mock("@rp/react/tanstack-api/watchlist", () => ({
  ...jest.requireActual("@rp/react/tanstack-api/watchlist"),
  useWatchlist: jest.fn(),
  useAllWatchlists: jest.fn(),
}))

const mockUseUserBriefConfigs = useUserBriefConfigs as jest.Mock
const mockUseBriefConfigById = useBriefConfigById as jest.Mock

jest.mock("@rp/react/tanstack-api/brief", () => ({
  ...jest.requireActual("@rp/react/tanstack-api/brief"),
  useUserBriefConfigs: jest.fn(),
  useBriefConfigById: jest.fn(),
}))

const mockNavigate = jest.fn()

jest.mock("react-router-dom", () => ({
  ...jest.requireActual("react-router-dom"),
  useNavigate: () => mockNavigate,
}))

beforeEach(jest.clearAllMocks)

describe("BriefCreateOrEdit", () => {
  describe("rendering", () => {
    describe("empty data", () => {
      beforeEach(() => {
        mockUseWatchlist.mockReturnValue({ data: [], isError: false, isLoading: false })
        mockUseUserBriefConfigs.mockReturnValue({ data: [], isError: false, isLoading: false })
        mockUseAllWatchlists.mockReturnValue({ data: [], isLoading: false })
        mockUseBriefConfigById.mockReturnValue({
          data: mockBriefConfigs[0],
          isError: false,
          isLoading: false,
        })
        mockUseBriefCreateOrEditSteps.mockReturnValue({
          step: mockStep,
          setActiveStep: () => {},
          activeStep: 0,
          numSteps: 3,
        })
        mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

        renderWithProviders(<BriefCreateOrEdit />)
      })

      it("should not have loading spinner", () => {
        expect(screen.queryByTestId("loading-spinner-container")).not.toBeInTheDocument()
      })

      it("should have header", () => {
        expect(screen.getByRole("header", { name: "brief page header" })).toBeInTheDocument()
      })

      it('should not have "brief limit reached" component', () => {
        expect(
          screen.queryByRole("article", { name: "brief limit reached" }),
        ).not.toBeInTheDocument()
      })

      it('should have brief stepper container"', () => {
        expect(screen.getByRole("article", { name: "brief stepper container" })).toBeInTheDocument()
      })
    })

    describe("loading", () => {
      describe("when isLoadingWatchlistPreload is true", () => {
        beforeEach(() => {
          mockUseWatchlist.mockReturnValue({
            data: [],
            isError: false,
            isLoading: true,
          })
          mockUseUserBriefConfigs.mockReturnValue({
            data: [],
            isError: false,
            isLoading: false,
          })
          mockUseBriefConfigById.mockReturnValue({
            data: undefined,
            isError: false,
            isLoading: false,
          })
          mockUseAllWatchlists.mockReturnValue({ data: [], isLoading: false })
          mockUseBriefCreateOrEditSteps.mockReturnValue({
            step: mockStep,
            setActiveStep: () => {},
            activeStep: 0,
            numSteps: 3,
          })
          mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

          renderWithProviders(<BriefCreateOrEdit />)
        })

        it("should have header", () => {
          expect(
            screen.queryByRole("header", { name: "brief page header" }),
          ).not.toBeInTheDocument()
        })

        it('should not have "brief limit reached" component', () => {
          expect(
            screen.queryByRole("article", { name: "brief limit reached" }),
          ).not.toBeInTheDocument()
        })

        it('should have brief stepper container"', () => {
          expect(
            screen.queryByRole("article", { name: "brief stepper container" }),
          ).not.toBeInTheDocument()
        })
      })
    })

    describe("with data", () => {
      describe("at step 0", () => {
        beforeEach(() => {
          mockUseWatchlist.mockReturnValue({
            data: mockWatchlists,
            isError: false,
            isLoading: false,
          })
          mockUseUserBriefConfigs.mockReturnValue({
            data: mockBriefConfigs.slice(0, 2),
            isError: false,
            isLoading: false,
          })
          mockUseBriefConfigById.mockReturnValue({
            data: mockBriefConfigs[0],
            isError: false,
            isLoading: false,
          })
          mockUseAllWatchlists.mockReturnValue({ data: mockWatchlists, isLoading: false })
          mockUseBriefCreateOrEditSteps.mockReturnValue({
            step: mockStep,
            setActiveStep: () => {},
            activeStep: 0,
            numSteps: 3,
          })
          mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

          renderWithProviders(<BriefCreateOrEdit isEdit />)
        })
        it("should have loading spinner", () => {
          expect(screen.queryByTestId("loading-spinner-container")).not.toBeInTheDocument()
        })

        it("should have header", async () => {
          expect(screen.getByRole("header", { name: "brief page header" })).toBeInTheDocument()
        })

        it('should not have "brief limit reached" component', () => {
          expect(
            screen.queryByRole("article", { name: "brief limit reached" }),
          ).not.toBeInTheDocument()
        })

        it('should have brief stepper container"', () => {
          expect(
            screen.getByRole("article", { name: "brief stepper container" }),
          ).toBeInTheDocument()
        })

        it("should have variable styling on stepper container", () => {
          expect(screen.getByRole("article", { name: "brief stepper container" })).toHaveStyle({
            "margin-bottom": "60px",
            "padding-bottom": "40px",
          })
        })
      })

      describe("not at step 0", () => {
        beforeEach(() => {
          mockUseWatchlist.mockReturnValue({ data: mockWatchlists, isError: false })
          mockUseUserBriefConfigs.mockReturnValue({
            data: mockBriefConfigs.slice(0, 2),
            isError: false,
          })
          mockUseBriefConfigById.mockReturnValue({
            data: mockBriefConfigs[0],
            isError: false,
            isLoading: false,
          })
          mockUseAllWatchlists.mockReturnValue({ data: mockWatchlists, isLoading: false })
          mockUseBriefCreateOrEditSteps.mockReturnValue({
            step: mockStep,
            setActiveStep: () => {},
            activeStep: 1,
            numSteps: 3,
          })
          mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

          renderWithProviders(<BriefCreateOrEdit isEdit />)
        })
        it("should have loading spinner", () => {
          expect(screen.queryByTestId("loading-spinner-container")).not.toBeInTheDocument()
        })

        it("should have header", () => {
          expect(screen.getByRole("header", { name: "brief page header" })).toBeInTheDocument()
        })

        it('should not have "brief limit reached" component', () => {
          expect(
            screen.queryByRole("article", { name: "brief limit reached" }),
          ).not.toBeInTheDocument()
        })

        it('should have brief stepper container"', () => {
          expect(
            screen.getByRole("article", { name: "brief stepper container" }),
          ).toBeInTheDocument()
        })

        it("should have variable styling on stepper container", () => {
          expect(screen.getByRole("article", { name: "brief stepper container" })).toHaveStyle({
            "margin-bottom": undefined,
            "padding-bottom": undefined,
          })
        })
      })

      describe("on last step", () => {
        describe("when saving", () => {
          beforeEach(() => {
            mockUseWatchlist.mockReturnValue({ data: mockWatchlists, isError: false })
            mockUseUserBriefConfigs.mockReturnValue({
              data: mockBriefConfigs.slice(0, 2),
              isError: false,
            })
            mockUseBriefConfigById.mockReturnValue({
              data: mockBriefConfigs[0],
              isError: false,
              isLoading: false,
            })
            mockUseAllWatchlists.mockReturnValue({ data: mockWatchlists, isLoading: false })
            mockUseBriefCreateOrEditSteps.mockReturnValue({
              step: mockStep,
              setActiveStep: () => {},
              activeStep: 2,
              numSteps: 3,
            })
            mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: true })

            renderWithProviders(<BriefCreateOrEdit isEdit />)
          })

          it("should complete button with spinner", () => {
            expect(
              within(screen.getByTestId("vertical-scroll-stepper-footer-button")).getByRole(
                "progressbar",
              ),
            ).toBeInTheDocument()
          })
        })

        describe("when not saving, creating", () => {
          beforeEach(() => {
            mockUseWatchlist.mockReturnValue({ data: mockWatchlists, isError: false })
            mockUseUserBriefConfigs.mockReturnValue({
              data: mockBriefConfigs.slice(0, 2),
              isError: false,
            })
            mockUseBriefConfigById.mockReturnValue({
              data: mockBriefConfigs[0],
              isError: false,
              isLoading: false,
            })
            mockUseAllWatchlists.mockReturnValue({ data: mockWatchlists, isLoading: false })
            mockUseBriefCreateOrEditSteps.mockReturnValue({
              step: mockStep,
              setActiveStep: () => {},
              activeStep: 2,
              numSteps: 3,
            })
            mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

            renderWithProviders(<BriefCreateOrEdit isEdit={false} />)
          })
          it("should complete button with spinner", () => {
            expect(screen.getByTestId("vertical-scroll-stepper-footer-button")).toHaveTextContent(
              "Create Brief",
            )
          })
        })
      })
    })

    describe("with data, exceed brief limit", () => {
      beforeEach(() => {
        mockUseWatchlist.mockReturnValue({ data: mockWatchlists, isError: false })
        mockUseUserBriefConfigs.mockReturnValue({
          data: mockBriefConfigs,
          isError: false,
        })
        mockUseBriefConfigById.mockReturnValue({
          data: mockBriefConfigs[0],
          isError: false,
          isLoading: false,
        })
        mockUseAllWatchlists.mockReturnValue({ data: mockWatchlists, isLoading: false })
        mockUseBriefCreateOrEditSteps.mockReturnValue({
          step: mockStep,
          setActiveStep: () => {},
          activeStep: 1,
          numSteps: 3,
        })
        mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

        renderWithProviders(<BriefCreateOrEdit isEdit={false} />)
      })

      it("should have loading spinner", () => {
        expect(screen.queryByTestId("loading-spinner-container")).not.toBeInTheDocument()
      })

      it("should have header", () => {
        expect(screen.getByRole("header", { name: "brief page header" })).toBeInTheDocument()
      })

      it('should have "brief limit reached" component', () => {
        expect(screen.getByRole("article", { name: "brief limit reached" })).toBeInTheDocument()
      })

      it('should not have brief stepper container"', () => {
        expect(
          screen.queryByRole("article", { name: "brief stepper container" }),
        ).not.toBeInTheDocument()
      })

      it("should not have submit button with correct text", () => {
        expect(screen.queryByRole("button", { name: "Next" })).not.toBeInTheDocument()
      })
    })
  })

  describe("functionality", () => {
    describe("click back", () => {
      beforeEach(() => {
        mockUseWatchlist.mockReturnValue({ data: [], isError: false })
        mockUseUserBriefConfigs.mockReturnValue({ data: [], isError: false })
        mockUseBriefConfigById.mockReturnValue({
          data: mockBriefConfigs[0],
          isError: false,
          isLoading: false,
        })
        mockUseAllWatchlists.mockReturnValue({ data: [], isLoading: false })
        renderWithProviders(<BriefCreateOrEdit />)

        fireEvent.click(screen.getByRole("button", { name: "back" }))
      })
      mockUseCreateBriefCallbacks.mockReturnValue({ isSaving: false })

      it("should call navigate with correct param", () => {
        expect(mockNavigate).toHaveBeenCalledTimes(1)
        expect(mockNavigate).toHaveBeenCalledWith(-1)
      })
    })
  })
})

